<template>
    <div class="device-revocation-row">
        <div class="div-success-left"><b>{{detail.money}} 元</b></div>
        <div class="device-revocation-detail">            
            <mt-cell label="收款方信息"></mt-cell>
            <mt-cell :label="detail.payee"></mt-cell>
            <mt-cell :label="detail.payeeAccount"></mt-cell>
            <mt-cell label="附言"></mt-cell>
            <mt-cell :label="detail.remark"></mt-cell>
        </div>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/internal-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.internalTransfer.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            this.$router.push('/two-dimension-code/payment/success');
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateInternalTransferSearch', this.detail );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .div-success-left {
        height: 90px;
        line-height: 90px;
        text-align: left;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
        color: #ffc107;
    }
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
</style>



