<template>
    <div style="background: #fff">
        <div class="account-success-img">
            <img src="../../../../assets/img/success.png">
            <span style="font-size: 50px;"><b>转账成功</b></span>
            <span>实时到账</span>
        </div>
        <div  class="account-success-info">
            <div class="bank-account-index">
                <img src="../../../../assets/img/default.png">
                <span>鸿</span>
                <span class="bank-account-name">安徽农金</span>
                <span class="bank-account-num">622218****00001</span>
            </div>
            <div class="bank-account-index bank-account-arrow">
                <img src="../../../../assets/img/arrow.png">
                <span class="bank-account-name">{{this.accountsDetail.money}} 元</span>
                <span class="bank-account-num">{{this.accountsDetail.remark}}</span>
            </div>
            <div class="bank-account-index">
                <img src="../../../../assets/img/default.png">
                <span>{{this.accountsDetail.payeeName}}</span>
                <span class="bank-account-name">{{this.accountsDetail.payeeBankName}}</span>
                <span class="bank-account-num">{{this.accountsDetail.account}}</span>
            </div>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toAccountIndex()">继续转账</mt-button>
            <mt-button size="large" type="primary" @click = "toInternalTransferIndex()">返回转账首页</mt-button>
        </div>
    </div>
</template>

<script>

// import * as service from '@/services/bank/accounts';

export default {
    data() {
        return {
            accountsDetail: [],
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            }
        };
    },
    created() {
        this.accountsDetail = Object.assign({}, this.$store.state.externalTransfer.search);
        console.log(this.accountsDetail);
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toAccountIndex() {
            // this.$router.go(-3);
            this.$router.push('/accounts/index');
        },
        toInternalTransferIndex() {
            this.$router.push('/internal-transfer/index');
        }
    }
};
</script>


<style lang="scss">
.account-success-img {
    text-align: center;
    border-bottom: 3px solid #a9acb1;
    margin: 10px 5px 10px 10px;
    padding-bottom: 10px;
    span {
        display: block;
        padding: 10px;
    }
}
.account-success-info{
    text-align: center;
    border-bottom: 3px solid #a9acb1;
    padding-bottom: 10px;
    .bank-account-index {
        margin-left: -300px;
        padding: 10px 0 10px 0;
        img {
            
        }
        span {
            position: absolute;
            display: inline-block;
            width: 300px;
            text-align: left;
            padding-left: 20px;
        }
        .bank-account-name {
            margin-top: 30px;
        }
        .bank-account-num {
            margin-top: 60px;
        }
    }
    .bank-account-arrow {
        img {
            height: 200px;
        }
        .bank-account-name {
            margin-top: 50px;
            font-size: 20px;
            color: #ffc107;
        }
        .bank-account-num {
            margin-top: 90px;
        }
    }
}
</style>



