<template>
    <div>
        <div class="two-dimension-code-receivables">
            <span>安徽农金手机银行APP扫一扫，向我付款</span>
            <img src="../../../../../../assets/img/bank/QR_code.png">
            <span>二维码每5分钟自动刷新</span>
            <span style="color: #26a2ff;" @click = "refresh()">现在刷新</span>
        </div>
        <div>
            <div class="field-part">
                <mt-cell label="收款人" value="123"></mt-cell>
                <mt-cell title="收款账户" is-link @click.native=" showSelectorPaymentAccount() "
                    :value="  dataForm.account || '请选择卡号' " is-link ></mt-cell>
            </div>
        </div>
        <div class="field-action">
            <mt-button @click.native="back()" size="large" type="primary">返回</mt-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
    </div>
</template>

<script>

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: false,
                accountList: []
            },
            dataForm: {
                account: '',
                data: []
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.internalTransfer.search);
        service.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
            this.dataForm.account = res.data[0].account;
        }, (err) => {
            console.log( err );
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
        back() {
            this.$ls.setItem("two-dimension-code-back", "code");
            this.$router.go(-1);
        },
        refresh (){
            console.log('refresh');
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.account;
            }, ( data ) => {});
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateInternalTransferSearch', this.detail );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.two-dimension-code-receivables {
    background: #fff;
    border: solid 1px #a9acb1;
    text-align: center;
    img {
        width: 450px;
    }
    span {
        display: block;
        margin: 20px;
    }
}
</style>



