<template>
    <div style="background: #fff">
        <div class="account-success-img">
            <img src="../../../../../../assets/img/success.png">
            <span style="font-size: 50px;"><b>转账成功</b></span>
        </div>
        <mt-cell label="转出金额：" value="1000"></mt-cell>
        <mt-cell label="收款人账户：" value="1000"></mt-cell>
        <mt-cell label="收款人户名：" value="1000"></mt-cell>
        <mt-cell label="付款账户：" value="1000"></mt-cell>
        <mt-cell label="附言：" value="1000"></mt-cell>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toAccountIndex()">继续转账</mt-button>
            <mt-button size="large" type="primary" @click = "toInternalTransferIndex()">返回转账首页</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            accountsDetail: [],
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            }
        };
    },
    created() {
      this.accountsDetail = this.$ls.getItem('twoDimensionCodePaymentSuccess');
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toCodeIndex() {
            this.$ls.setItem("two-dimension-code-back", "code");
            // this.$router.go(-3);
            this.$router.push('/accounts/index');
        },
        toInternalTransferIndex() {
            this.$router.push('/internal-transfer/index');
        }
    }
};
</script>


<style lang="scss">
.account-success-img {
    text-align: center;
    border-bottom: 3px solid #a9acb1;
    margin: 10px 5px 10px 10px;
    padding-bottom: 10px;
    span {
        display: block;
        padding: 10px;
    }
}
</style>



