<template>
    <div class="transaction-detail">
        <div class="field-part">
            <cell class="item"
                title="付款账户" :value="dataForm.account" :desc="dataForm.showMoney"
                @click.native="showSelectorPaymentAccount()"  is-link>
            </cell>
            <cell-transfer-money 
                title="转账金额" placeholder="请输入转账金额" 
                :totalMoney="dataForm.showMoney" v-model="dataForm.money">
            </cell-transfer-money>
        </div>
        <span style="height: 60px;line-height: 60px;padding-right: 20px;text-align: end;display: block;" @click="showSelectorTransferAging()">{{dataForm.patternName}}，<label style="color: #26a2ff;">更换到账时间</label></span>
        <div class="field-part field-group">
            <mt-field label="收款人" placeholder="收款人"  v-model=" dataForm.payeeName " disableClear>
            </mt-field>
            <mt-field label="收款账户" placeholder="收款账号" type="number" v-model=" dataForm.payeeAccount " disableClear></mt-field>
            <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                <i class="icon"></i>
            </router-link>
        </div>
        <div class="field-part">
            <mt-cell title="收款银行" href="#/selector/bank"  :value=" dataForm.bankName || '请选择银行' " is-link>
            </mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="开户地" href="#/selector/bank-city"  :value=" dataForm.bankCity || '请选择开户地址' " is-link>
            </mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="分支行" href="#/selector/bank-branch"  :value=" dataForm.bankBranch || '请选择支行' " is-link>
            </mt-cell>
        </div>
        <div class="field-part my-payee-img">
            <mt-field label="附言" placeholder="选填" v-model="dataForm.remark" :maxlength="3"></mt-field>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">转账附言70字以内</span>
        <div class="field-action">
            <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" size="large" type="submit"></submit-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
        <selector-transfer-aging ref="selectorTransferAging" :options=" selectorTransferAging " ></selector-transfer-aging>
    </div>
	
</template>

<script>

import { Navbar, TabItem, Toast } from 'mint-ui-rem-dpr';
import { mapGetters, mapActions, mapState } from 'vuex';

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            selected: '1',
            submitStatus: false,
            dataForm: {
                payeeName: '',
                payeeAccount: '',
                payeeBankName: '',
                bankName: '',
                bankCity: '',
                bankBranch: '',
                account: '',
                showMoney: '',
                money: '',
                remark: '',
                pattern: '02',
                patternName: '普通到账'
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: false,
                accountList: []
            },
            selectorTransferAging: {
                title: '选择到账时间',
                checked: '',
                instant: true,
                general: true,
                morrow: true
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.externalTransfer.search);
        this.dataForm = Object.assign(this.dataForm, this.$store.getters.transactionDemo);
        var dimensionCode = this.$ls.getItem('two-dimension-code-back');
        if(dimensionCode == 'code'){
            this.selected = '2';
            this.$ls.removeItem('two-dimension-code-back');
        }
        service.getAccountList().then((res) => {
            if(!this.dataForm.money){
                this.dataForm.account = res.data[0].ACCOUNT;
            }
            console.log(res.data[0]);
            this.dataForm.showMoney = '可用余额：' + res.data[0].money;
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            // if( this.$validate.isEmpty( _data.account ) ) {
            //     _error.push( '银行卡号不能为空。' );
            // }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
    		var params = {
    			path: '/external-transfer/pay',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.ACCOUNT;
                this.dataForm.showMoney = '可用余额：' + data.item.money;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item;
                this.dataForm.pattern = data.item.code;
                this.dataForm.patternName = data.item.text;
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.money && item.payeeName && item.payeeAccount && item.payeeBankName && item.bankName && item.bankCity && item.bankBranch){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateExternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearExternalTransferSearch');
            this.$store.dispatch('clearPayee');
            this.$store.dispatch('clearBank');
            this.$store.dispatch('clearBankCity');
            this.$store.dispatch('clearBankBranch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-payee-img {
    position: relative;
    img {
        width: 80px;
        position: absolute;
        top: 55px;
        right: 10px;
    }
}
.two-dimension-code {
    margin: 0 auto;
    width: 400px;
   .two-dimension-code-items {
        height: 400px;
        line-height: 400px;
        text-align: center;
        background: #e0dec0;
        margin-top: 100px;
        span {
            display: block;
            height: 100px;
            line-height: 300px;
        }
    } 
}
.btn-seletor-payee
{
    background-color: #fff;
    .icon
    {
        background-image: url('../../../../assets/img/selector/icon-address-book.png');
    }
}
</style>



