<template>
    <div class="device-revocation-row">
        <transfer-success-pay 
            :payer='detail.account' payerAccount='' payerBank='' 
            :payee='detail.payeeName' :payeeAccount='detail.payeeAccount' :payeeBank='detail.payeeBankName' 
            :payeeMoney='detail.money' :remark='detail.remark' transferType='4'></transfer-success-pay>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <bankCard></bankCard>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/external-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.externalTransfer.search);
        console.log(this.detail);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            service.getExternalTransferSuccess().then((res) => {
                this.$router.push('/external-transfer/success');
            }, (err) => {
                console.log( err );
            });
        }
    }
};
</script>


<style lang="scss">

</style>



