<template>
    <div class="device-revocation-row">
        <div class="div-success-left"><b>{{detail.money}} 元</b></div>
        <div class="device-revocation-detail">            
            <span style="color: #ccc;">收款方信息</span>
            <span>{{detail.payeeName}}</span>
            <span>{{detail.payeeBankName}}</span>
            <span>{{detail.account}}</span>
        </div>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>

import * as service from '@/services/bank/external-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.agriculturalCreditTransfer.search);
        console.log(this.detail);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            service.getExternalTransferSuccess().then((res) => {
                this.$router.push('/agricultural-credit-transfer/success');
            }, (err) => {
                console.log( err );
            });
        }
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .div-success-left {
        height: 90px;
        line-height: 90px;
        text-align: left;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
        color: #ffc107;
    }
    .device-revocation-detail {
        background: #fff;
        span {
            display: block;
            padding: 5px 0 5px 15px;
        }
        
    }
}
</style>



