<template>
    <div style="background: #fff">
        <tip-page tipTitle="转账成功" tipMsg="实时转账"></tip-page>
        <transfer-success-detail 
            :payer='accountsDetail.account' payerAccount='' payerBank='' 
            :payee='accountsDetail.payeeName' :payeeAccount='accountsDetail.payeeAccount' :payeeBank='accountsDetail.payeeBankName' 
            :payeeMoney='accountsDetail.money' :remark='accountsDetail.remark' transferType='4'></transfer-success-detail>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toIndex()">返回首页</mt-button>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary" @click = "toAccountIndex()">继续转账</mt-button>
            <mt-button size="large" type="primary" @click = "toInternalTransferIndex()">返回转账首页</mt-button>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            accountsDetail: []
        };
    },
    created() {
        this.accountsDetail = Object.assign({}, this.$store.state.internalTransfer.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        toIndex() {
            this.$router.push('/');
        },
        toAccountIndex() {
            // this.$router.go(-3);
            this.$router.push('/accounts/index');
        },
        toInternalTransferIndex() {
            this.$router.push('/internal-transfer/index');
        }
    }
};
</script>


<style lang="scss">
</style>



