<template>
	<div>
        <div class="field-part">
            <mt-cell title="收款账户" is-link @click.native=" showSelectorPaymentAccount() "
                :value="  dataForm.account || '请选择卡号' " is-link ></mt-cell>
            <mt-cell label="可用余额" value="1000"></mt-cell>
            <mt-cell label="转账金额" value="20"></mt-cell>
            <mt-cell label="到账说明" value="实时到账"></mt-cell>
            <mt-cell label="收款人" value="kanade"></mt-cell>
            <mt-cell label="收款账户" value="62221****1230"></mt-cell>
            <mt-field label="附言：" placeholder="选填" v-model="dataForm.remark"></mt-field>
        </div>
        <span style="display: block;padding: 10px 0 0 10px;color: #329eef;">转账附言70字以内</span>
        <div class="field-action">
            <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" type="submit"></submit-button>
        </div>
        <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>   
    </div>
</template>

<script>

import { Navbar, TabItem, Toast } from 'mint-ui-rem-dpr';
import { mapGetters, mapActions, mapState } from 'vuex';

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            submitStatus: false,
            selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: false,
                accountList: []
            },
            dataForm: {
                account: '',
                remark: '',
                data: []
            }
        };
    },
    created(){
        // this.dataForm = Object.assign({}, this.$store.state.internalTransfer.search);
        service.getAccountList().then((res) => {
            if(!this.dataForm.money){
                this.dataForm.account = res.data[0].account;
            }
            this.dataForm.showMoney = '可用余额：' + res.data[0].account;
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            if( this.$validate.isEmpty( _data.account ) ) {
                _error.push( '银行卡号不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
    		var params = {
    			path: '/two-dimension-code/payment/pay',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.account;
                this.dataForm.showMoney = '可用余额：' + data.item.account;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item.code;
                this.dataForm.pattern = data.item.text;
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.account){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateInternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-payee-img {
    position: relative;
    img {
        width: 80px;
        position: absolute;
        top: 55px;
        right: 10px;
    }
}
</style>



