<template>
    <div>
        <div class="transaction-detail">
            <div class="field-part">
                <div class="cell-part">
                    <account-select title="付款账户" :value=" dataForm.account || '请选择卡号' " 
                        :balance="selectorPaymentAccount.checked.balance"
                        type="complex"
                        @click.native=" showSelectorPaymentAccount(1) "></account-select>
                </div>
                <cell-transfer-money 
                    title="转账金额" placeholder="请输入转账金额" 
                    :totalMoney="selectorPaymentAccount.checked.balance" v-model="dataForm.money">
                </cell-transfer-money>
            </div>
            <span class="pattern-span">实时到账</span>
            <div class="payee-account-detail">
                <cell class="item"
                    title="收款账户" :value="dataForm.payeeAccount" @click.native="showSelectorPaymentAccount(2)"  is-link>
                </cell>
            </div>
            <div class="field-action">
                <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" size="large" type="submit"></submit-button>
            </div>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
        </div>
    </div>
	
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            selected: '1',
            submitStatus: false,
            dataForm: {
                checked: {},
                payeeChecked: {},
                account: '',
                accountName: '',
                accountBank: '',
                money: '',
                payeeAccount: '',
                payeeName: '',
                payeeBank: ''
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                payeeChecked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.myAccountTransfer.search);
        service.getAccountList().then((res) => {
            if(!this.dataForm.account){
                let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
                console.log(defaultAccount);
                this.selectorPaymentAccount.checked = defaultAccount;
                this.dataForm.account = defaultAccount.ACCOUNT;
                this.dataForm.accountName = defaultAccount.name;
                this.dataForm.checked = defaultAccount;
            }else{
                this.selectorPaymentAccount.checked = this.dataForm.checked;
            }
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            // var _error = [];
            // if( this.$validate.isEmpty( _data.account ) ) {
            //     _error.push( '银行卡号不能为空。' );
            // }
            // if( _error.length > 0 ) {
            //     Toast({
            //         message: _error[0],
            //         iconClass: 'error',
            //         duration: 1000
            //     });
            //     return;
            // }
    		var params = {
    			path: '/my-account-transfer/pay',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount( type ) {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                if( type == '1'){
                    this.selectorPaymentAccount.checked = data.item;
                    this.dataForm.account = data.item.ACCOUNT;
                    this.dataForm.accountName = data.item.name;
                    this.dataForm.checked = data.item;
                }else{
                    this.selectorPaymentAccount.payeeChecked = data.item;
                    this.dataForm.payeeAccount = data.item.ACCOUNT;
                    this.dataForm.payeeName = data.item.name;
                    this.dataForm.payeeChecked = data.item;
                }
            }, ( data ) => {});
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.account && item.money && item.payeeAccount){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateMyAccountTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearMyAccountTransferSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.payee-account-detail {
    margin-top: 84px;
}
.pattern-span {
    position: absolute;
    padding-top: 20px;
    right: 24px;
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: #666666;
    line-height: 24px;
}
.btn-seletor-payee
{
    background-color: #fff;
    .icon
    {
        background-image: url('../../../../assets/img/selector/icon-address-book.png');
    }
}
</style>



