<template>
    <div>
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">行内转账</mt-tab-item>
          <mt-tab-item id="2">二维码转账</mt-tab-item>
        </mt-navbar>
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">
            <div class="transaction-detail">
                <div class="field-part">
                    <div class="cell-part">
                        <account-select title="卡号" :value=" dataForm.account || '请选择卡号' " 
                            :balance="selectorPaymentAccount.checked.balance"
                            type="complex"
                            @click.native=" showSelectorPaymentAccount() "></account-select>
                    </div>
                    <cell-transfer-money 
                        title="转账金额" placeholder="请输入转账金额" 
                        :totalMoney="selectorPaymentAccount.checked.balance" v-model="dataForm.money">
                    </cell-transfer-money>
                </div>
                <div class="field-part field-group filter-payee">
                    <mt-field label="收款人" placeholder="收款人"  v-model=" dataForm.payeeName " disableClear>
                    </mt-field>
                    <mt-field label="收款账户" placeholder="收款账号" type="number" v-model=" dataForm.payeeAccount " disableClear></mt-field>
                    <router-link to="/selector/payee" class="btn-selector btn-seletor-payee">
                        <i class="icon"></i>
                    </router-link>
                </div>
                <span class="pattern-span" @click="showSelectorTransferAging()">{{dataForm.patternName}}，<label style="color: #26a2ff;">更换到账时间</label></span>
                <div class="field-part payee-account-remark">
                    <mt-field label="附言" placeholder="选填" v-model="dataForm.remark" :maxlength="3"></mt-field>
                </div>
                <span class="payee-account-remark-dest">转账附言70字以内</span>
                <div class="field-action">
                    <submit-button @submit="search()" :submitStatus="submitStatus" value="下一步" size="large" type="submit"></submit-button>
                </div>
                <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
                <selector-transfer-aging ref="selectorTransferAging" :options=" selectorTransferAging " ></selector-transfer-aging>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="two-dimension-code">
                <div class="two-dimension-code-items" @click= "payment()">
                    <span style="font-size: 50px"><b>付款</b></span>
                    <span>扫一扫二维码，向对方转账</span>
                </div>
                <div class="two-dimension-code-items" @click= "receivables()">
                    <span style="font-size: 50px"><b>收款</b></span>
                    <span>显示我的二维码，向对方收钱</span>
                </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
    </div>
	
</template>

<script>

import { Navbar, TabItem, Toast } from 'mint-ui-rem-dpr';
import { mapGetters, mapActions, mapState } from 'vuex';

import * as service from '@/services/bank/selector';

export default {
    data() {
        return {
            selected: '1',
            submitStatus: false,
            dataForm: {
                checked: {},
                account: '',
                payeeName: '',
                payeeAccount: '',
                payeeBankName: '',
                money: '',
                remark: '',
                pattern: '02',
                patternName: '普通到账'
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                type: 'payment',
                checked: {},
                showAddBtn: false,
                showWarn: true,
                showBankName: false,
                accountList: []
            },
            selectorTransferAging: {
                title: '选择到账时间',
                checked: '',
                instant: true,
                general: true,
                morrow: true
            }
        };
    },
    created(){
        this.dataForm = Object.assign({}, this.$store.state.internalTransfer.search);
        this.dataForm = Object.assign(this.dataForm, this.$store.getters.transactionDemo);
        var dimensionCode = this.$ls.getItem('two-dimension-code-back');
        if(dimensionCode == 'code'){
            this.selected = '2';
            this.$ls.removeItem('two-dimension-code-back');
        }
        service.getAccountList().then((res) => {
            if(!this.dataForm.account){
                let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
                this.selectorPaymentAccount.checked = defaultAccount;
                this.dataForm.account = defaultAccount.ACCOUNT;
                this.dataForm.checked = defaultAccount;
            }else{
                this.selectorPaymentAccount.checked = this.dataForm.checked;
            }
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            if( this.$validate.isEmpty( _data.account ) ) {
                _error.push( '银行卡号不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
    		var params = {
    			path: '/internal-transfer/pay',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.ACCOUNT;
                this.dataForm.checked = data.item;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item;
                this.dataForm.pattern = data.item.code;
                this.dataForm.patternName = data.item.text;
            }, ( data ) => {});
        },
        payment() {
            this.$router.push("/two-dimension-code/payment/index");
        },
        receivables() {
            this.$router.push("/two-dimension-code/receivables");
        }
    },
    watch: {
        dataForm: {
            handler(item){
                if(item.account && item.money && item.payeeName && item.payeeAccount){
                    this.submitStatus = true;
                }else{
                    this.submitStatus = false;
                }
            },
            deep: true
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateInternalTransferSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
            this.$store.dispatch('clearPayee');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-payee-img {
    position: relative;
    img {
        width: 80px;
        position: absolute;
        top: 55px;
        right: 10px;
    }
}
.filter-payee {
    margin-top: 20px;
}
.pattern-span {
    position: absolute;
    padding-top: 20px;
    right: 24px;
    font-family: PingFangSC-Regular;
    font-size: 13px;
    color: #666666;
    line-height: 24px;
}
.payee-account-remark {
    margin-top: 84px;
}
.payee-account-remark-dest {
    display: inline-block;
    padding: 20px 0px 0px 50px;
    opacity: 0.8;
    font-family: PingFangSC-Regular;
    font-size: 12px;
    color: #B5B5B5;
    line-height: 12px;
}
.two-dimension-code {
    margin: 0 auto;
    width: 400px;
   .two-dimension-code-items {
        height: 400px;
        line-height: 400px;
        text-align: center;
        background: #e0dec0;
        margin-top: 100px;
        span {
            display: block;
            height: 100px;
            line-height: 300px;
        }
    } 
}
.btn-seletor-payee
{
    background-color: #fff;
    .icon
    {
        background-image: url('../../../../assets/img/selector/icon-address-book.png');
    }
}
</style>



