import ajax from '@/services/ajax';
import api from '@/services/api';

export function getAccountsIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getAccountsIndex );
}

export function getDeviceRevocationIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getDeviceRevocationIndex );
}

export function getDeviceRevocationList(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getDeviceRevocationList );
}

export function getDeviceRevocationDetail(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getDeviceRevocationDetail );
}



