import ajax from '@/services/ajax';
import api from '@/services/api';

export function getInternalTransferPay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getInternalTransferPay );
}

export function getInternalTransferSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getInternalTransferPay );
}



