import ajax from '@/services/ajax';
import api from '@/services/api';

export function getExternalTransferIndex(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getExternalTransferIndex );
}

export function getExternalTransferPay(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getExternalTransferPay );
}

export function getExternalTransferSuccess(data) {
    return ajax.postAfterLoginQuery( '12345', data, api.getExternalTransferSuccess );
}



