<template>
    <div class="device-revocation-row">
        <transferSuccessPay 
            :payer='detail.account' payerAccount='' payerBank='' 
            :payee='detail.payeeName' :payeeAccount='detail.payeeAccount' :payeeBank='detail.payeeBankName' 
            :payeeMoney='detail.money' :remark='detail.remark' transferType='1'></transferSuccessPay>
        <div class="detail-items">
            <cell-password :options=" cellPassword " ></cell-password>
        </div>
        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
        <div class="field-action">
            <mt-button @click="showDialogSmsVerify()" size="large" type="primary">确认</mt-button>
        </div>
    </div>
</template>

<script>
import * as service from '@/services/bank/internal-transfer';

export default {
    data() {
        return {
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入短信验证码',
                btnConfirmText: '确定转账',
                btnGetText: '获取验证码'
            },
            detail: {}
        };
    },
    created() {
        this.detail = Object.assign({}, this.$store.state.internalTransfer.search);
    },
    computed: {
    },
    components: {
    },
    methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                alert('短信验证码验证成功...');
                this.next();
            }, (data) => {
            });
        },
        next(id) {
            service.getInternalTransferSuccess().then((res) => {
                this.$router.push('/internal-transfer/success');
            }, (err) => {
                console.log( err );
            });
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateInternalTransferSearch', this.detail );
        }else{
            this.$store.dispatch('clearInternalTransferSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    .div-success-left {
        height: 90px;
        line-height: 90px;
        text-align: left;
        font-size: 30px;
        background: #fff;
        border: 1px solid #ddd;
        color: #ffc107;
    }
    .device-revocation-detail {
        background: #fff;
        span {
            display: block;
            padding: 5px 0 5px 15px;
        }
        
    }
}
</style>



